body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-image:url("/images/background.gif");
  /* background-repeat: no-repeat; */
  /* background-size: cover; */
  width:100vw;
  overflow: auto;
}
a{
  text-decoration: none;
}
.nav{
  width:100vw;
  background-color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.navitem{
  color:rgb(3, 189, 3);
  font-weight: bold;
  font-family: arial;
  font-size: 2vw;
  margin:1.5rem;
}
.navitem:hover{
  color:whitesmoke;
}
.container{
  width:100%;
}
.homediv{
  background-color:rgba(211, 211, 211, 0.7);
  width:65%;
  border-radius: .75rem;
  margin:3rem;
  padding:.5rem;
}
.content{
  width:100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.homepara{
  font-size: 1.5rem;
}
hr{
  border:1px solid black;
  margin:0%;
}
.searchbody{
  background-color:rgba(211, 211, 211, 0.7);
  width:65%;
  border-radius: .75rem;
  margin:3rem;
  padding:.5rem;
}
.searchnav{
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  display: -webkit-flex;
  display: flex;
}
.searchbar{
  border-radius:.3rem;
  margin:.25rem;
  height: 1.2rem;
}
.searchButton{
  height:1.5rem;
  border-radius: .25rem;
  margin:.25rem;
}
.heads{
  float: left;
  margin:.5rem;
}
.searchResults{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: row wrap;
          flex-wrap: row wrap;
}
.savedNav{
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
}
.header{
  font-size:1.5rem;
  font-weight: bold;
  margin:.25rem;
}
